.canvas-wrapper {
    position: fixed;
    left: 0px;
    right: 0px;
    top: 0px;
    bottom: 0px;
    overflow: hidden;
    margin: 0;
    padding: 0;
}

.canvas {
    width: 100%;
    height: 100%;
    padding: 0;
    margin: 0;

    z-index: 0;

    :root[data-theme='light'] & {
        filter: invert(1);
    }
}

.gradient {
    position: absolute;
    background: var(--backcloth);
    background: radial-gradient(
        circle,
        var(--primary) 0%,
        var(--backcloth) 100%
    );

    :root[data-theme='light'] & {
        background: radial-gradient(
            circle,
            var(--backcloth) 10%,
            rgba(var(--primary-rgb), 0.15) 100%
        );
    }

    width: 100vw;
    height: 100vh;
    z-index: 1;

    mix-blend-mode: screen;

    :root[data-theme='light'] & {
        mix-blend-mode: multiply;
    }
}
